/* breakpoints */
:root {
  --gray100: #161616;
  --gray90: #262626;
  --white: #fff;
  ---back-2: #282828;
  ---tekts-bold-: #e2eaf0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

*:active,
*:focus {
  outline: none;
}

html {
  min-height: 100vh;
  height: 100%;
  overflow-x: hidden;
}

body {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: normal;
  color: #ffffff;
  height: 100%;
  background: #181818 url("../img/body_bg.svg") no-repeat right top -72.5%;
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

a,
input {
  font: inherit;
  color: inherit;
  text-decoration: none;
  transition: all 0.3s ease;
}

a:hover {
  color: #ff562f;
}

a.btn:focus {
  box-shadow: none;
}

input:focus {
  outline: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
}

.wrapper {
  max-width: 1641px;
  margin-inline: auto;
}

.btn {
  font-weight: 700;
  font-size: 20px;
  color: #eee;
  padding: 10px 40px;
  border: 1px solid #eee;
  border-radius: 123px;
}
.btn:hover {
  color: #ffffff;
  background-color: #ff562f;
}
.btn--gray {
  border: none;
  background: #4d4e4f;
}
.btn--gray:hover {
  color: initial;
  background-color: #d4deef;
}
.btn--secondary {
  background-color: #ff562f;
  border: none;
}
.btn--secondary:hover {
  color: #ffffff;
  background-color: #181818;
}

@media (max-width: 560px) {
  .wrapper {
    max-width: 360px;
    margin-inline: auto;
  }
  .btn {
    font-size: 10px;
    padding: 7px 20px;
  }
  .mobile-hide {
    display: none;
  }
}
.header {
  color: #ffffff;
}
.header .wrapper {
  padding-block: 40px 22px;
  border-bottom: 1px solid #d6d6d6;
  display: flex;
  align-items: center;
}
.header__menu-toggle {
